.posts-page{
    background-color: var(--background-color);
    padding: 1% 10%;
    display: flex;
    flex-direction: row;
}

.post-list-container{
    width: 75%;
    max-height: 95vh;
    overflow-y: auto;
    margin: 0;
    list-style-type: none;
    padding: 0;
    padding-right: 20px;
}

.loading-centered {
    position: absolute;
    left: 50%;
}